/*--------------------------------------------------------------
# Heroes

1. Mixins
2. Hero, Desktop
3. Hero, Mobile
4. Story Hero, Mobile
5. Story Hero, Desktop
--------------------------------------------------------------*/
/*--------------------------------------------------------------
  2. Hero, Desktop
  --------------------------------------------------------------*/
.uds-hero {
  background-size: cover;
  background-position: top;
  width: 100%;
  height: 42.75rem;
  display: flex;
  max-width: 1920px; }
  .uds-hero .uds-hero-container {
    width: 800px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: auto auto 2rem auto; }
    .uds-hero .uds-hero-container h1 {
      margin-left: 0; }
    .uds-hero .uds-hero-container .uds-hero-text {
      margin-left: 0; }
    .uds-hero .uds-hero-container p {
      color: #ffffff;
      font-size: 1.5rem;
      margin-bottom: 1.75rem; }
  .uds-hero.uds-hero-sm {
    height: 16rem; }
    .uds-hero.uds-hero-sm .container.uds-hero-container {
      width: 100%; }
  .uds-hero.uds-hero-md {
    height: 32rem; }
    .uds-hero.uds-hero-md .container {
      width: 100%; }
      .uds-hero.uds-hero-md .container .uds-hero-text {
        line-height: 2rem; }
  .uds-hero.uds-hero-lg {
    height: 42.75rem; }
    .uds-hero.uds-hero-lg .container.uds-hero-container {
      width: 100%; }
      .uds-hero.uds-hero-lg .container.uds-hero-container .uds-hero-text {
        line-height: 2rem; }

/*--------------------------------------------------------------
  3. Hero, Mobile
  --------------------------------------------------------------*/
@media screen and (max-width: 576px) {
  .uds-hero .uds-hero-container {
    margin-bottom: -2rem; }
    .uds-hero .uds-hero-container h1 {
      max-width: 100%; }
    .uds-hero .uds-hero-container p {
      display: none; } }

@media screen and (max-width: 576px) {
  .uds-hero.uds-hero-sm {
    padding-left: 1.5rem;
    padding-bottom: 2rem; } }

@media screen and (max-width: 1400px) {
  .uds-hero.uds-hero-lg {
    height: 32rem; } }

/*--------------------------------------------------------------
  4. Story Hero, Mobile
  --------------------------------------------------------------*/
.uds-story-hero {
  display: grid;
  grid-template-columns: 1rem 1fr 1rem;
  grid-template-rows: calc(16rem - 4.5rem) 4.5rem auto;
  gap: 0px 0px;
  max-width: 1920px;
  margin: 0 auto; }
  .uds-story-hero .hero {
    grid-column: 1 / span 3;
    grid-row: 1 / span 2;
    object-fit: cover;
    height: 100%;
    max-width: 100%; }
  .uds-story-hero .content {
    grid-column: 2 / span 1;
    grid-row: 2 / span 2;
    background-color: #ffffff;
    padding: 0 1rem; }
    .uds-story-hero .content .breadcrumb,
    .uds-story-hero .content p.meta {
      padding: .75rem 0;
      margin: 0.25rem 0; }
    .uds-story-hero .content h1 {
      margin-top: 0; }

/*--------------------------------------------------------------
  5. Story Hero, Desktop
  --------------------------------------------------------------*/
@media (min-width: 950px) {
  .uds-story-hero {
    grid-template-columns: 1.5rem 1fr 1.5rem;
    grid-template-rows: calc(32rem - 8rem) 8rem 1fr; }
    .uds-story-hero.uds-story-hero-lg {
      grid-template-rows: calc(42.75rem - 8rem) 8rem 1fr; }
    .uds-story-hero .content {
      padding: 0 calc(8.34% + 12px); }
      .uds-story-hero .content .breadcrumb,
      .uds-story-hero .content p.meta {
        margin: 1.25rem 0 2.25rem; } }

@media (min-width: calc(1224px)) {
  .uds-story-hero {
    grid-template-columns: 1fr calc(1200px + 12px + 12px) 1fr; } }

.single article {
  padding: 1rem; }

.single article > * {
  margin-left: auto;
  margin-right: auto; }

@media (min-width: 950px) {
  .single article > * {
    margin-left: calc(8.34% + 1rem);
    margin-right: calc(8.34% + 1rem); } }

@media (min-width: calc(1224px)) {
  .single article > * {
    max-width: 995px;
    margin-left: auto;
    margin-right: auto; } }
